import axios from 'axios';
import React, {useEffect, useState} from 'react';
// import { useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
  padding : 20px;
`;

let Title = styled.h4`
  font-size : 25px;
  color : ${ props => props.color }
`;

// class Detail2 extends React.Component{

//   componentDidMount(){


//   }

//   componentWillUnmount(){

 
//   }
// }



function Detail(props){

  let[alertFlag, setAlertFlag] = useState(true);
  let[inputData, setInputData] = useState('');
  //컴포넌트가 mount되었을때, 컴포넌트가 update될때 특정 코드를 실행.
  useEffect(()=>{
    
    let alertTimer = setTimeout(()=>{ setAlertFlag(false); }, 2000);

    //컴포넌트가 사라질 때 실행
    return () => {
      clearTimeout(alertTimer);
    }
    //alertFlag가 변경될때만 실행하도록 조건 추가.
  },[alertFlag]);
  // []경우 조건이 없기때문에 페이지 로딩할때 최초 1번만 실행됨.
  // },[]);


  let history = useHistory(); //방문기록 등을 저장해놓는 object
  let { id } = useParams(); //hook
  // let findProduct = props.shoes.find( x => x.id == id);
  let findProduct = props.shoes.find(function(product){
    return product.id == id;
  });
    
    return(
      <div className="container">
        <Box>
          <Title className="red">상세페이지</Title>
          {/* <Title color={'red'}>상세페이지</Title>
          <Title color="blue">상세페이지</Title> */}
        </Box>

        { inputData }
        <input onChange={(e)=>{ setInputData(e.target.value); }} />

        {
          alertFlag === true
          ? <div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다!!!</p>
            </div>
          : null

        }

        <div className="row">
          <div className="col-md-6">
            <img src={findProduct.url} width="100%" />
            {/* <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" /> */}
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{findProduct.title}</h4>
            <p>{findProduct.content}</p>
            <p>{findProduct.price}</p>
            <Info product={props.product}></Info>



            <button className="btn btn-danger" onClick={()=>{
              // let array = [...props.product];
              // console.log(array);
              // console.log(props.product);
              // array[0] = array[0]-1;

              props.setProduct([9,10,11]);


            }}>주문하기</button> 
            &nbsp;
            <button className="btn btn-danger" onClick={()=>{

                console.log("history");
                console.log(history);
                // history.goBack(); // 뒤로가기
                history.push('/'); // 해당경로로 이동

              }}>뒤로가기</button> 
          </div>
        </div>
      </div> 
    )
}


function Info(props){
  return(
    <p>재고 : {props.product[0]}</p>
  )

}




export default Detail;